.company-list {
    padding: 10px;
  }
  
  .company-list h2 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .company-list ul {
    list-style: none;
    padding: 0;
  }
  
  .company-list li {
    padding: 8px;
    margin: 5px 0;
    cursor: pointer;
    background: #e9e9e9;
    border-radius: 4px;
  }
  
  .company-list li:hover {
    background: #dcdcdc;
  }